#table-box select {
  height: auto;
  display: block;
  min-width: 200px;
}

.table-group {
  display: inline-block;
}
.table-group select {
  min-width: 60px;
  height: 30px;
}

.column-item {
  display: block;
  width: auto;
  height: 50px;
  line-height: 50px;
}
.column-item span {
  min-width: 30%;
  display: inline-block;
  text-align: center;
}

.copy-panel {
  margin-top: 30px;
}
.copy-panel .panel-header .dist-item,
.copy-panel .panel-header .table-item {
  position: relative;
  background-color: #fff;
  line-height: 30px;
  padding: 5px 10px;
  border-radius: 3px;
  cursor: pointer;
}
.copy-panel .panel-header .dist-item .fa,
.copy-panel .panel-header .table-item .fa {
  position: absolute;
  right: -10px;
  top: -8px;
  background: #333;
  color: #fff;
  border-radius: 50%;
  display: block;
  width: 20px;
  height: 20px;
  line-height: 20px;
  text-align: center;
}
.copy-panel .panel-header .dist-item:hover,
.copy-panel .panel-header .table-item:hover {
  background-color: red;
  color: #fff;
}
.copy-panel .panel-header .table-item {
  margin-right: 20px;
}
.copy-panel span[data-action=column-select] {
  cursor: pointer;
}
.copy-panel .column-item {
  display: flex;
}
.copy-panel .column-item i {
  font-style: normal;
  line-height: 50px;
}
.copy-panel .src-column,
.copy-panel .dist-column {
  flex: 1;
  word-break: break-all;
}

.dialog-select,
.dialog-column-select {
  display: none;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.2);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.dialog-select .dialog-body,
.dialog-column-select .dialog-body {
  position: absolute;
  top: 40vh;
  left: 50vw;
  width: 300px;
  margin-left: -150px;
  background-color: white;
}
.dialog-select .dialog-body select,
.dialog-select .dialog-body button,
.dialog-column-select .dialog-body select,
.dialog-column-select .dialog-body button {
  height: 50px;
  min-width: 80px;
  border: none;
  outline: none;
}

@media (max-width: 767px) {
  .zd-tab .zd-tab-head .zd-tab-item {
    width: 32%;
  }
}